https://cloud.tencent.com/developer/article/1942722

https://zhuanlan.zhihu.com/p/27407024

better-scroll: https://better-scroll.github.io/docs/zh-CN/
 -->

<template>
  <div ref="wrapper" :class="{ wrapper: true, x: props.scrollX }">
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>

<script setup>
import { onMounted, ref } from 'vue'
import BScroll from '@better-scroll/core'
import ObserveDOM from '@better-scroll/observe-dom'
import ObserveImage from '@better-scroll/observe-image'
import MouseWheel from '@better-scroll/mouse-wheel'
import Pullup from '@better-scroll/pull-up'
import Pulldown from '@better-scroll/pull-down'

let bscroll = BScroll
const wrapper = ref(null)
BScroll.use(ObserveDOM)
BScroll.use(ObserveImage)
BScroll.use(MouseWheel)
BScroll.use(Pullup)
BScroll.use(Pulldown)
const emit = defineEmits(['ckick', 'beforeScroll', 'afterScroll', 'scroll'])
// const emit = defineEmits(["ckick", "beforeScroll", "afterScroll", "scroll", "backTop"]);

const props = defineProps({
  /**
   * 1 scroll
   * 2 scroll
   * 3 scrollswipescroll
   *
   *  v2.1.0  probeType 
   * 1. probeType  0 scroll 
   * 2. probeType  1 momentumLimitTime  scroll 
   * 3. probeType  2 scroll 
   * 4. probeType  3 scroll  scrollTo  momentum 
   */
  probeType: {
    type: Number, // Number: 1, 2, 3
    required: false,
    default: 1
  },

  /**
   * 
   * display:inline-block
   */
  /**
   *  true 
     eventPassthrough  'vertical' 
   */
  // scrollX: {
  scrollY: {
    type: Boolean,
    required: false,
    default: false
  },

  /**
   * click
   * BetterScroll  click  trueBetterScroll  click  event  _constructed true
   */
  click: {
    type: Boolean,
    default: true
  },
  /**
   * 
   */
  listenScroll: {
    type: Boolean,
    default: false
  },
  /**
   * 
   */
  pullup: {
    type: Function,
    default: null
  },
  /**
   * 
   */
  pulldown: {
    type: Function,
    default: null
  },
  /**
   * 
   */
  beforeScroll: {
    type: Boolean,
    default: false
  },
  /**
   * 
   */
  afterScroll: {
    type: Boolean,
    default: false
  },
  /**
   *  true  Boolean:  || ,
   * 
   */
  bounce: {
    type: Object,
    // eslint-disable-next-line vue/require-valid-default-prop
    default: {
      left: false, // 
      right: false, // 
      top: false,
      bottom: true
    }
  }
})

onMounted(() => {
  // DOMbetter-scroll
  setTimeout(() => {
    _initScroll()
  }, 20)
})

const _initScroll = () => {
  bscroll = new BScroll(wrapper.value, {
    scrollX: props.scrollX,
    probeType: props.probeType,
    click: props.click,
    bounce: props.bounce,
    observeDOM: true,
    observeImage: true,
    mouseWheel: true,
    pullDownRefresh: true,
    pullUpLoad: true
  })
  /**
   * 
   */
  if (props.beforeScroll) {
    bscroll.on('beforeScrollStart', () => {
      emit('beforeScroll')
    })
  }

  /**
   * ()
   */
  if (props.listenScroll) {
    // bscroll.on('scroll', (position: { x: number; y: number }) => {
    bscroll.on('scroll', (position) => {
      emit('scroll', position)
    })
  }

  /**
   * 
   */
  if (props.beforeScroll) {
    bscroll.on('scrollEnd', () => {
      emit('afterScroll')
    })
  }

  // 
  if (props.pullup !== null) {
    bscroll.on('pullingUp', () => {
      try {
        props.pullup().then(() => {
          bscroll.finishPullUp()
        })
      } catch (e) {
        //  Promise 
        // bscroll.finishPullUp();
      }
    })
  }

  if (props.pulldown !== null) {
    bscroll.on('pullingDown', () => {
      try {
        props.pulldown().then(() => {
          bscroll.finishPullDown()
        })
      } catch (e) {
        //  Promise 
        bscroll.finishPullDown()
      }
    })
  }
}

const disable = () => {
  // better-scrolldisable
  bscroll && bscroll.disable()
}
const enable = () => {
  // better-scrollenable
  bscroll && bscroll.enable()
}
const refresh = () => {
  // better-scrollrefresh
  bscroll && bscroll.refresh()
}

function scrollTo() {
  // better-scrollscrollTo
  bscroll && bscroll.scrollTo.apply(bscroll, arguments)
}
// const scrollToElement = () => {
function scrollToElement() {
  // better-scrollscrollToElement
  bscroll && bscroll.scrollToElement.apply(bscroll, arguments)
}

const finishPullUp = () => {
  bscroll && bscroll.finishPullUp && bscroll.finishPullUp()
}

// const closePullUp = () => {
//   bscroll && bscroll.closePullUp && bscroll.closePullUp();
// };

defineExpose({
  disable,
  enable,
  refresh,
  scrollTo,
  scrollToElement,
  finishPullUp
  // closePullUp,
})
</script>

<style lang="scss" scoped>
.wrapper {
  overflow: hidden;
  // scroll
  position: absolute;
  top: 43.71px;
  bottom: 49px;
  left: 0;
  right: 0;
  .content {
    display: block;
  }
  // 
  &.x {
    white-space: nowrap;
    .content {
      display: inline-block;
    }
  }
}
</style>
